import React, { useEffect } from 'react';
import Banner from '../../components/common/Banner';
import { PromoteBoard, PromoteNews } from '../../components/Components';

import { ContVideo } from './PromoteComponent';


export default function OSP6111() {
  
  const page = {
    code: 6,
    subcode: -1
  };

  // 홍보 영상 쪽으로 내려오면 애니메이션 시작하기 
  const handleEventByScroll = () => {
    const articlePromotion = document.querySelector('.promotion-overview');
    const articlePromotionHeight = articlePromotion.getBoundingClientRect().top;
    const articlePromotionScrollY = Math.floor(window.scrollY + articlePromotionHeight);

    // ! 500은 임의 값
    if(window.scrollY > articlePromotionScrollY - 500){
      articlePromotion.classList.add('--animation');
    }
  }

  useEffect(() => {
    window.addEventListener('scroll', handleEventByScroll);
    return () => {
      window.removeEventListener('scroll', handleEventByScroll)
    }
  }, []);
  
  return <>
    <Banner page={page} />

    <main className='main-osp6111'>
      <PromoteBoard />
      <PromoteNews />

      <article className='promotion-overview'>
        <section className='video w-default'>
          <div className='background'></div>
          
          <h2 className='title'>홍보 영상</h2>

          <ul className='media-list'>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
          </ul>

          {/* 수정 - 버튼 */}
          <a className="btn-more" href="/">
            <span>홍보 영상 전체 보기
              <i className="icon icon-more_arrow">
              <svg width="18" height="11" viewBox="0 0 18 11" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M12.5 2L11.795 2.705L14.085 5L2 5L2 6L14.085 6L11.79 8.295L12.5 9L16 5.5L12.5 2Z" fill="#ffffff"/>
              </svg>
              </i>
            </span>
          </a>

        </section>
      </article>
    </main>
  </>
}