import React, { useState } from 'react'

import Banner from "../../components/common/Banner"
import { TabBarStyle } from '../../components/common/TabStyle';
import { InputSearchBoard } from '../../components/common/InputSearch';
import { Pagination } from '../../components/common/ForBoard';
import { IconDownLoad } from '../../components/common/Icons';

import MainOSP0113 from './OSP0113';

const SectionOSP0112 = ({ tos, page }) => {
  return <section className='section-osp0112'>
    <div className='w-default tos-download'>
      <InputSearchBoard page={page} />
      <ul className='products-tos-list'>
        {
          tos.map((item, index) => {
            const { name, ver, size } = item;

            return <li key={index}>
              <div className='tos-product'>
                <span className='title'>{name}</span>
                <span className='ver'>{ver}</span>
                <p><strong>크기</strong>{"  "}{size}</p>
              </div>

              <div className='btns-downloads'>
                <a href='#downloads' className='btn btn-rounded-01 commu --install'>
                  <span>설치파일 	&#40;&#46;iso&#41;</span>
                  <IconDownLoad />
                </a>
                <a href='#downloads' className='btn btn-rounded-03 commu'>릴리즈 노트</a>
              </div>
            </li>
          })
        }
      </ul>
      <Pagination nums={[1, 2, 3, 4, 5]} commu={true} />
      </div>
  </section>
}


export default function OSP0112() {
  const [tabNumber, setTabNumber] = useState(0);

  const page = {
    code: 8,
    subcode: 0,
    community: true,
    inputCate: {
      banner: false,
      name: "search-table",
      placeHolder: '버전 검색'
    }
  };

  const tos = [
    {
      name: 'TmaxGooroom',
      ver: 'ver 3.0',
      size: '1.5GB'
    },
    {
      name: 'TmaxGooroom',
      ver: 'ver 2.4',
      size: '1.5GB'
    },
    {
      name: 'TmaxGooroom',
      ver: 'ver 2.3',
      size: '1.5GB'
    },
  ];

  // tab에 따라서 다른 컴포넌트를 보여준다. 
  const toggleContents = (tabNumber: number) => {
    switch (tabNumber) {
      case 0:
        return <SectionOSP0112 tos={tos} page={page} />;
      case 1:
        return <MainOSP0113 />;
      default:
        return <>
          잘못된 접근
          history 관리하기
        </>;
    }
  }

  const onClick = (event) => {
    const { target: { value } } = event;
    if (value) {
      setTabNumber(Number(value));
    }
  }


  return <>
    <Banner page={page} />
    <TabBarStyle props={{
      list: [
        'Tmax구름 다운로드', '소스 코드'
      ],
      commu: true,
      tabNumber,
      setTabNumber
    }}
    />


    {toggleContents(tabNumber)}
  </>
}