            <path d="M20.5093 6.7269C21.0254 6.7269 21.4437 7.14524 21.4437 7.66129L21.4437 8.27341C21.4437 8.78946 21.0254 9.2078 20.5093 9.2078L1.53439 9.20775C1.01834 9.20775 0.599998 8.78941 0.599998 8.27336L0.599998 7.66125C0.599998 7.1452 1.01834 6.72685 1.53439 6.72685L20.5093 6.7269Z" fill="#B0B0B0"/>
