/**
 * @InputSearchBanner : 배너에 있는 통합검색창
 * @InputSearchBoard : 게시판에 있는 게시판검색창
 */
import { useState } from "react";
// import { useNavigate } from "react-router-dom";


// 배너에 있는 검색 창
const InputSearchBanner = ({page}) => {
  // input value
  const [ val, setVal ] = useState('');

  // 돋보기 아이콘 활성화 
  const [ label, setLabel ] = useState(false);
  
  const inf = { // page default 객체
    community: false,
    inputCate : {
      placeholder: 'default',
    },
    ...page
  };

  // set input value
  const onChange = (event) => { 
    const { target : {value}} = event;
    setVal(value);
  };

  return <form 
      className={inf.community?"search-banner commu":"search-banner"}
      // onSubmit
//     // method get action= "/app/OSP3111" //when 고객지원
//     // method get action= "/app/OSP7112" //when 구름 커뮤니티
    >

    <label>
      <i className={label ? "icon icon-search focused":"icon icon-search"}>
        <svg viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" clipRule="evenodd" d="M14.6308 15.7372C13.4207 16.7063 11.885 17.2859 10.214 17.2859C6.30856 17.2859 3.14258 14.1199 3.14258 10.2145C3.14258 6.30905 6.30856 3.14307 10.214 3.14307C14.1194 3.14307 17.2854 6.30905 17.2854 10.2145C17.2854 11.8855 16.7058 13.4211 15.7367 14.6313C15.7564 14.6475 15.7755 14.6648 15.7939 14.6832L19.5444 18.4337C19.8512 18.7405 19.8512 19.238 19.5444 19.5448C19.2375 19.8517 18.74 19.8517 18.4332 19.5448L14.6827 15.7944C14.6643 15.776 14.647 15.7569 14.6308 15.7372ZM15.714 10.2145C15.714 13.2521 13.2516 15.7145 10.214 15.7145C7.17644 15.7145 4.71401 13.2521 4.71401 10.2145C4.71401 7.17693 7.17644 4.71449 10.214 4.71449C13.2516 4.71449 15.714 7.17693 15.714 10.2145Z" fill="#C4C4C4" />
        </svg>
      </i>
    </label>

    <input name="search" id="search" type='text'
      className={val.length>0 ? "typing":" "}
      placeholder={inf.inputCate.placeHolder}
      value={val}
      onChange={(event) => onChange(event)}
      onClick={() => setLabel(true)}
    />

    {
      // 닫기 버튼
      val.length > 0 &&
      <button 
        className="btn-close" 
        onClick={() => {
          setLabel(false);
          setVal("");
        }}
      >
        <i className="icon icon-search_clear">
          <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M4.9322 5.05027C4.68457 5.29878 4.68457 5.7017 4.9322 5.95022L8.96767 10L4.93224 14.0498C4.6846 14.2983 4.6846 14.7013 4.93224 14.9498C5.17987 15.1983 5.58137 15.1983 5.829 14.9498L9.86444 10.9L13.8999 14.9498C14.1475 15.1983 14.549 15.1983 14.7966 14.9498C15.0443 14.7012 15.0443 14.2983 14.7966 14.0498L10.7612 10L14.7967 5.95023C15.0443 5.70171 15.0443 5.29879 14.7967 5.05027C14.549 4.80176 14.1475 4.80176 13.8999 5.05027L9.86444 9.10008L5.82897 5.05027C5.58134 4.80175 5.17984 4.80175 4.9322 5.05027Z" fill="#797979" />
          </svg>
        </i>
      </button>
    }
  </form>
}


// 게시판에 있는 검색창
const InputSearchBoard = ({page}) => {
  // input value
  const [ val, setVal ] = useState('');

  // 돋보기 아이콘 활성화 
  const [ label, setLabel ] = useState(false);

  // page default 객체
  const inf = { 
    community: false,
    inputCate : {
      placeholder: 'default',
    },
    ...page
  };

  // set input value
  const onChange = (event) => {
    const { target : {value}} = event;
    setVal(value);
  };

  return <form 
      className={inf.community? "search-table commu":"search-table"}
      style={{display: 'none'}}
    >

    <label>
      <i className={label?"icon icon-search focused":"icon icon-search"}>
        <svg viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" clipRule="evenodd" d="M14.6308 15.7372C13.4207 16.7063 11.885 17.2859 10.214 17.2859C6.30856 17.2859 3.14258 14.1199 3.14258 10.2145C3.14258 6.30905 6.30856 3.14307 10.214 3.14307C14.1194 3.14307 17.2854 6.30905 17.2854 10.2145C17.2854 11.8855 16.7058 13.4211 15.7367 14.6313C15.7564 14.6475 15.7755 14.6648 15.7939 14.6832L19.5444 18.4337C19.8512 18.7405 19.8512 19.238 19.5444 19.5448C19.2375 19.8517 18.74 19.8517 18.4332 19.5448L14.6827 15.7944C14.6643 15.776 14.647 15.7569 14.6308 15.7372ZM15.714 10.2145C15.714 13.2521 13.2516 15.7145 10.214 15.7145C7.17644 15.7145 4.71401 13.2521 4.71401 10.2145C4.71401 7.17693 7.17644 4.71449 10.214 4.71449C13.2516 4.71449 15.714 7.17693 15.714 10.2145Z" fill="#91989D" />
        </svg>
      </i>
    </label>

    <input name="search-table" id="search-table" type='text'
      className={val.length > 0 ? "typing":" "}
      placeholder={inf.inputCate.placeHolder}
      value={val}
      onChange={(event) => onChange(event)}
      onClick={() => setLabel(true)}
    />
    {
      val.length > 0 &&
      <button 
        className="btn-close" 
        onClick={() => {
          setVal("");
          setLabel(false);
        }}
      >
        <i className="icon icon-search_clear">
          <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M4.9322 5.05027C4.68457 5.29878 4.68457 5.7017 4.9322 5.95022L8.96767 10L4.93224 14.0498C4.6846 14.2983 4.6846 14.7013 4.93224 14.9498C5.17987 15.1983 5.58137 15.1983 5.829 14.9498L9.86444 10.9L13.8999 14.9498C14.1475 15.1983 14.549 15.1983 14.7966 14.9498C15.0443 14.7012 15.0443 14.2983 14.7966 14.0498L10.7612 10L14.7967 5.95023C15.0443 5.70171 15.0443 5.29879 14.7967 5.05027C14.549 4.80176 14.1475 4.80176 13.8999 5.05027L9.86444 9.10008L5.82897 5.05027C5.58134 4.80175 5.17984 4.80175 4.9322 5.05027Z" fill="#797979" />
          </svg>
        </i>
      </button>
    }
  </form>
}

export { InputSearchBoard, InputSearchBanner };
