import { DropBox } from '../../components/common/ForForm';


export default function OSP8112() {
  return <section className="osp8112">
    <div className='banner'>
      <div className="w-default --top">
        <h2>티맥스오에스 정책</h2>
        <p>서비스 이용약관과 개인정보 처리 방침</p>
      </div>

      <div className='w-default --bottom'>
        <ul className='toggle'>
          <li><button className='focused'>이용약관</button></li>
          <li><button>개인정보 처리방침</button></li>
        </ul>
        <DropBox props={{
          label: '2021.12.31 개정'
        }}/>
      </div>
    </div>

    <div className="content w-default">
      <p>
        약관은 계약의 당사자가 다수의 상대방과 계약 체결을 위하여 미리 작성한 계약을 말하며, 이를 영문으로 기재한 것을 영문약관이라 한다. 영문약관이 일반적인 계약과 다른 점은 다수를 상대로 이전에 미리 작성해놓았다는 점이다.
      </p>
      <p>
        약관의 대표적인 종류로는 보험약관, 운송약관, 은행예금 약관 등이 있다. 영문약관의 내용은 계약 당사자 간의 권리와 의무에 대한 사항, 의무를 불이행했을 때 제재, 약관의 존속기간 등으로 구성된다.
      </p>
      <p>
        약관은 작성하는 회사나 기업의 종류와 특성에 따라 매우 다양해지며, 계약 체결 전 상대방에게 미리 정보를 제공하는 기능을 가진다. 상대방은 약관을 통해 계약 체결의 의사를 결정하게 되며, 실제로 계약을 체결하기 이전부터 잠정적인 합의를 이끌어낼 수 있다는 장점이 있다.<br />
        [네이버 지식백과] 영문약관 [英文約款] (예스폼 서식사전, 2013.)
      </p>
    </div>
  </section>
}