import { useState } from 'react';
import { Link } from 'react-router-dom';

import Banner from '../../components/common/Banner';
import { BoardNotice, QuestionBoard } from '../../components/CustomBoard';
import { TabBarStyle } from '../../components/common/TabStyle';

import { links } from '../../data/link';

export default function OSP7111() {
  const [tabNumber, setTabNumber] = useState(0);

  const btnList = [
    {
      name: '공지사항',
      span: '회사와 제품에 대한 새로운 소식을 전달합니다.',
      icon: 'icon-custom-01',
      path: links.osp7116
    },
    {
      name: '자주하는 질문',
      span: '티맥스오에스에 자주하는 질문과 답을 살펴보세요.',
      icon: 'icon-custom-02',
      path: links.osp7120
    },
    {
      name: '문의하기',
      span: '티맥스오에스에 대해 궁금한 점을 문의해 주세요.',
      icon: 'icon-custom-03',
      path: links.osp7121
    }
  ];

  const page = {
    code: 7,
    subcode: -1,
    community: false,
    inputCate: {
      banner: true,
      name: 'search',
      placeHolder: '검색어를 입력해 주세요.',
    }
  };

  return <>
    <Banner page={page} />
    
    <main className='main-osp7111 custom'>
      <section className='products background-01'>
        <span className='bg_c-l'></span>
        <span className='bg_c-r'></span>
        <h2>궁금한 점이 있으신가요?</h2>
        <ul className='menu-list'>
          {
            btnList.map((li, index) => {
              const { name, span, icon, path } = li;
              return <li key={index}>
                <Link to={path} className="btn-link-customer">
                  <i className={'icon ' + icon}></i>
                  <h6>{name}</h6>
                  <span>{span}</span>
                </Link>
              </li>
            })
          }
        </ul>
      </section>

      {/* 2 - 도움이 필요한 제품을 선택하세요 */}
      <section className='custom-products'>
        <h3>도움이 필요한 제품을 선택하세요.</h3>
        <ul className='products-list'>
          {
            ['Tmax구름', "Tmax구름SD", 'SuperRDS'].map((li, index) => {
              return <li key={index}>
                <button className='btn-product'>
                  <span className='icon-product'>{li}</span>
                  <i className='icon icon_costom-ar-r'></i>
                </button>
              </li>
            })
          }
        </ul>
      </section>

      <section className='custom-notice'>
        <h3>공지사항</h3>
        <div className='board-wrap notice w-default'>
          <BoardNotice />
          <a className='btn-more' href='#공지사항 전체보기'>공지사항 전체보기<i className='icon icon-more_arrow'></i></a>
        </div>
      </section>

      <section className='custom-question'>
        <h3>자주하는 질문</h3>
        <div className='board-wrap question w-default'>

          <TabBarStyle 
            props = {{
              list : [
                '전체', 'Tmax구름', 'SuperPCMaster', 'SuperVDS', '기타'
              ],
              tabNumber,
              setTabNumber
            }}
          />

          <QuestionBoard />
          <a className='btn-more' href='#공지사항 전체보기'>자주하는 질문 전체보기<i className='icon icon-more_arrow'></i></a>
        </div>
      </section>
    </main>
  </>
}