import React from 'react';
import Banner from '../../components/common/Banner';

import { ContNews } from './PromoteComponent';
import { Pagination } from '../../components/common/ForBoard';


export default function OSP6115() {
  const page = {
    code: 6,
    subcode: 0
  }
  return <>
    <Banner page={page} />

    <main className='main-osp6115'>
      <article className='promotion-words w-default'>
        <div className='wrap-item news'>
          <ul className='item-list'>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
          </ul>
          <Pagination />
        </div>
      </article>
    </main>
  </>
}
