/**
 * * Common Rules for Boards
 * commu, basic 
 * @Attachs - 게시판의 아이콘 세트들 
 * @Pagination - 게시판 밑바닥
 * * pagination commu
 */
import { useState } from 'react';


const Attachs = ({h4 = '', state={}}) => {
  const board = { //default
    notice: false,
    views: 0,
    img: false,
    file: false,
    zip: false,
    new: false,
    ...state
  };

  return<>
    <i className={board.notice?"icon icon-table-notice":"icon icon-table-notice --h"}></i>
    <h4>{h4}</h4>
    {
      board.views > 0 && 
      <span className="views">&#91;{board.views}&#93;</span>
    }
    <i className={board.img?"icon icon-img":"icon icon-img --n"}></i>
    <i className={board.file?"icon icon-file":"icon icon-file --n"}></i>
    <i className={board.zip?"icon icon-zip":"icon icon-zip --n"}></i>
    <i className={board.new?"icon icon-new":"icon icon-new --n"}></i>
  </>            
};


const Pagination = ({nums = [1, 2, 3, 4, 5], commu = false}) => {
  const [ numberNow, setNumberNow ] = useState(1);
  const paginationLength = nums.length;

  return <div className={commu? "pagination commu":"pagination"}>
    {/* --disable: 버튼 클릭 불가능  */}
    <button className="btn-ar --left --disable">
      <i className="icon">
        <svg className='svg' width="20" height="26" viewBox="0 0 20 26" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M11.2232 13L5.47459 18.7486C5.31262 18.9096 5.09352 19 4.86514 19C4.63677 19 4.41767 18.9096 4.2557 18.7486C4.17468 18.6683 4.11037 18.5727 4.06648 18.4673C4.0226 18.362 4 18.249 4 18.1349C4 18.0207 4.0226 17.9078 4.06648 17.8024C4.11037 17.6971 4.17468 17.6015 4.2557 17.5211L8.7768 13L4.25571 8.4789C4.17468 8.39854 4.11037 8.30293 4.06648 8.19759C4.0226 8.09225 4 7.97926 4 7.86514C4 7.75102 4.0226 7.63803 4.06648 7.53269C4.11037 7.42735 4.17468 7.33174 4.25571 7.25138C4.41767 7.09037 4.63677 7 4.86515 7C5.09352 7 5.31262 7.09037 5.47459 7.25138L11.2232 13Z" fill="#dddddd"/>
        <path d="M16.2232 13L10.4746 18.7486C10.3126 18.9096 10.0935 19 9.86514 19C9.63677 19 9.41767 18.9096 9.2557 18.7486C9.17468 18.6683 9.11037 18.5727 9.06648 18.4673C9.0226 18.362 9 18.249 9 18.1349C9 18.0207 9.0226 17.9078 9.06648 17.8024C9.11037 17.6971 9.17468 17.6015 9.2557 17.5211L13.7768 13L9.25571 8.4789C9.17468 8.39854 9.11037 8.30293 9.06648 8.19759C9.0226 8.09225 9 7.97926 9 7.86514C9 7.75102 9.0226 7.63803 9.06648 7.53269C9.11037 7.42735 9.17468 7.33174 9.25571 7.25138C9.41767 7.09037 9.63677 7 9.86515 7C10.0935 7 10.3126 7.09037 10.4746 7.25138L16.2232 13Z" fill="#dddddd"/>
        </svg>
      </i>
    </button>

    <button className={numberNow === 1 ? "btn-ar --left --disable" : "btn-ar --left --able"}
      onClick={() => {
        if(numberNow !== 1){
          setNumberNow(numberNow-1)
        }
      }}
    >
      <i className="icon">
      <svg className='svg' width="20" height="26" viewBox="0 0 20 26" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M13.2232 13L7.47459 18.7486C7.31262 18.9096 7.09352 19 6.86514 19C6.63677 19 6.41767 18.9096 6.2557 18.7486C6.17468 18.6683 6.11037 18.5727 6.06648 18.4673C6.0226 18.362 6 18.249 6 18.1349C6 18.0207 6.0226 17.9078 6.06648 17.8024C6.11037 17.6971 6.17468 17.6015 6.2557 17.5211L10.7768 13L6.25571 8.4789C6.17468 8.39854 6.11037 8.30293 6.06648 8.19759C6.0226 8.09225 6 7.97926 6 7.86514C6 7.75102 6.0226 7.63803 6.06648 7.53269C6.11037 7.42735 6.17468 7.33174 6.25571 7.25138C6.41767 7.09037 6.63677 7 6.86515 7C7.09352 7 7.31262 7.09037 7.47459 7.25138L13.2232 13Z" fill="#dddddd"/>
      </svg>
      </i>
    </button>

    {/* pagination 숫자들 */}
    <ol className='pagination-nums'>
      {
        nums.map((num, index) => {
        return <li key={index} className={index+1 === numberNow ? 'now' : ''}>
          <button
            onClick={() => {
              setNumberNow(index+1)
            }}
          >{num}</button>
        </li>
        })
      }
    </ol>

    {/* --able: 버튼 클릭 가능  */}
    <button 
      className={numberNow === paginationLength ? "btn-ar --right --disable" : "btn-ar --right --able"}
      onClick={() => {
        if(numberNow !== paginationLength){
          setNumberNow(numberNow+1)
        }
      }}
    >
      <i className="icon">
        <svg className='svg' width="20" height="26" viewBox="0 0 20 26" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M13.2232 13L7.47459 18.7486C7.31262 18.9096 7.09352 19 6.86514 19C6.63677 19 6.41767 18.9096 6.2557 18.7486C6.17468 18.6683 6.11037 18.5727 6.06648 18.4673C6.0226 18.362 6 18.249 6 18.1349C6 18.0207 6.0226 17.9078 6.06648 17.8024C6.11037 17.6971 6.17468 17.6015 6.2557 17.5211L10.7768 13L6.25571 8.4789C6.17468 8.39854 6.11037 8.30293 6.06648 8.19759C6.0226 8.09225 6 7.97926 6 7.86514C6 7.75102 6.0226 7.63803 6.06648 7.53269C6.11037 7.42735 6.17468 7.33174 6.25571 7.25138C6.41767 7.09037 6.63677 7 6.86515 7C7.09352 7 7.31262 7.09037 7.47459 7.25138L13.2232 13Z" fill="#dddddd"/>
        </svg>
      </i>
    </button>

    <button className="btn-ar --right --disable">
      <i className="icon">
        <svg className='svg' width="20" height="26" viewBox="0 0 20 26" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M11.2232 13L5.47459 18.7486C5.31262 18.9096 5.09352 19 4.86514 19C4.63677 19 4.41767 18.9096 4.2557 18.7486C4.17468 18.6683 4.11037 18.5727 4.06648 18.4673C4.0226 18.362 4 18.249 4 18.1349C4 18.0207 4.0226 17.9078 4.06648 17.8024C4.11037 17.6971 4.17468 17.6015 4.2557 17.5211L8.7768 13L4.25571 8.4789C4.17468 8.39854 4.11037 8.30293 4.06648 8.19759C4.0226 8.09225 4 7.97926 4 7.86514C4 7.75102 4.0226 7.63803 4.06648 7.53269C4.11037 7.42735 4.17468 7.33174 4.25571 7.25138C4.41767 7.09037 4.63677 7 4.86515 7C5.09352 7 5.31262 7.09037 5.47459 7.25138L11.2232 13Z" fill="#dddddd"/>
        <path d="M16.2232 13L10.4746 18.7486C10.3126 18.9096 10.0935 19 9.86514 19C9.63677 19 9.41767 18.9096 9.2557 18.7486C9.17468 18.6683 9.11037 18.5727 9.06648 18.4673C9.0226 18.362 9 18.249 9 18.1349C9 18.0207 9.0226 17.9078 9.06648 17.8024C9.11037 17.6971 9.17468 17.6015 9.2557 17.5211L13.7768 13L9.25571 8.4789C9.17468 8.39854 9.11037 8.30293 9.06648 8.19759C9.0226 8.09225 9 7.97926 9 7.86514C9 7.75102 9.0226 7.63803 9.06648 7.53269C9.11037 7.42735 9.17468 7.33174 9.25571 7.25138C9.41767 7.09037 9.63677 7 9.86515 7C10.0935 7 10.3126 7.09037 10.4746 7.25138L16.2232 13Z" fill="#dddddd"/>
        </svg>
      </i>
    </button>
  </div>
};



// 버튼 - 목록보기
const BtnLinkList = () => {
  return <a className='btn-link-list' href='#목록 보기'>
  <i className='icon icon-btn-table-list'></i>
  목록 보기
  </a>
}

// 버튼 - 문의하기 (검색 결과 없을 때,)
const BtnNoResult = () => {
  return <button className='btn btn-rounded-03 btn-no-result'>
    <span>문의하기</span>
    <i className="icon icon-write">
      <svg width="12" height="12" viewBox="0 4 25 22" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M17.4392 4.55253C18.4806 3.51113 20.169 3.51113 21.2104 4.55253C22.2518 5.59393 22.2518 7.28237 21.2104 8.32377L19.3245 10.2096L15.5533 6.4384L17.4392 4.55253Z" fill="white"/>
      <path d="M2.35422 19.6375L14.2027 7.78899L17.9739 11.5602L6.12545 23.4087L1.88281 23.8801L2.35422 19.6375Z" fill="white"/>
      </svg>
    </i>
  </button>
}

export { Pagination, Attachs, BtnLinkList, BtnNoResult };