import React from 'react'

import Banner from '../../components/common/Banner';
import { Pagination } from '../../components/common/ForBoard';
import { ContNews } from './PromoteComponent';


export default function OSP6112() {

  const page = {
    code: 6,
    subcode: 1
  }

  return <>
    <Banner page={page} />

    <main className='main-osp6112'>
      <section className='promotion-news w-default'>
        <div className='title-banner'>
          <div className="wrap-img">
            <img src={process.env.PUBLIC_URL + "/asset/images/02.png"} alt="temp_image" />
          </div>
          <div className='wrap-content'>
            <h1 className='title --pcview'>티맥스오에스, 구름 플랫폼 기반‘Tmax구름’개발</h1>
            <h1 className='title --mobileview'>
              <span> 티맥스오에스,</span>
              <span> 구름 플랫폼 기반‘Tmax구름’개발</span>
            </h1>

            <p className='content'>
              티맥스오에스가 구름 플랫폼 기반 개방형 OS 'Tmax구름'을 개발했다.
              <br />
              운영체제 전문기업 티맥스오에스(대표이사 허희도)는 과학기술정보통신부가 지원하고 국가보안기술연구소가 만든 개방형 보안OS인 구름 플랫폼(구름OS)을 기반으로 자사가 보유한 OS 개발 기술을 적용하여 PC용 운영체제 'Tmax구름'을 개발했다고 14일 밝혔다. 뉴스 내용 영역입...
            </p>
            <span className='date'>2021.12.13</span>
          </div>
        </div>

        <hr className='line' />

        <div className='wrap-item news'>
          <ul className='item-list'>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            <li>
              <ContNews />
            </li>
            
          </ul>

          <Pagination />
        </div>
      </section>
    </main>
  </>
}
