import { useState } from 'react';

import Banner from '../../components/common/Banner';

import { TabBarStyle } from '../../components/common/TabStyle';
import { BoardNotice } from '../../components/CustomBoard';
import { InputSearchBoard } from '../../components/common/InputSearch';
import { Pagination } from '../../components/common/ForBoard';

export default function OSP7116() {
  const [tabNumber, setTabNumber] = useState(0);
  
  const page = {
    code: 7,
    subcode: 0,
    inputCate: {
      banner: false,
      name: "search-table",
      placeHolder: '검색어를 입력해 주세요.'
    }
  };

  return <>
    <Banner page={page} />
    
    <TabBarStyle 
      props = {{
        list : [
          '전체', '일반', '제품'
        ],
        tabNumber,
        setTabNumber
      }}
    />

    <section className='section-osp7116 custom'>
      <div className='width1352'>
        <InputSearchBoard page={page} />
        
        <div className='board-wrap notice'>
          <div className='title'>
            <span>분류</span>
            <span>제목</span>
            <span>날짜<i className='icon icon-sort'></i></span>
          </div>
      
          <BoardNotice />
      
          <Pagination nums={[1, 2, 3, 4, 5]}/>
        </div>
      </div>
    </section>
  </>
}