import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

// import Banner from './Banner';
import { IconHamberger, IconLnbClose } from '../common/Icons';
import { ButtonLinkHR } from '../button/Buttons';
import { ButtonNaviLogoHome, ButtonNaviGnb } from '../button/Navi';
import { ButtonScrollTop, ButtonDepthMore } from './Header.Nav';

import { links } from '../../data/link';

export default function Header(props) {
  // console.log(props.page);

  const navSites = [
    {
      h5: 'TmaxOS 소개',
      path: links.osp4111,
      img_banner: 'img_banner-subpage-1', // img넣을 class이름 
      span: [
        '개방형 OS 시장을 선도하고 신사업을 발굴해 다방면의 서비스를 제공합니다.',
      ],
      ul: [
        {
          path: links.osp4111,
          text: '회사소개',
          p: [
            '개방형 OS 시장을 선도하고 신사업을 발굴해 다방면의 서비스를 제공합니다.',
          ],
        },
        {
          path: links.osp4116,
          text: '연혁',
          p: [
            '대한민국을 넘어 글로벌 SW기업으로 도약하기 위해 도전하고 상상합니다.',
          ],
        },
        {
          path: links.osp4117,
          text: '오시는 길',
          p: [
            '대한민국을 넘어 글로벌 SW기업으로 도약하기 위해 도전하고 상상합니다.'
          ],
        },
      ]
    },
    {
      h5: '제품',
      path: links.osp5111,
      img_banner: 'img_banner-subpage-2',
      span: [
        'Tmax구름, SuperPCMaster 등 티맥스오에스의 제품을 소개합니다.',
      ],
      ul: [
        {
          path: links.osp5112,
          text: 'Tmax구름',
          p: [
            '국가보안기술연구소의 구름 보안 프레임워크에',
            'TmaxOS 자체 개발 기술력으로 완성한 개방형 운영 체제'
          ],
        },
        {
          path: links.osp5113,
          text: 'Tmax구름 SD',
          p: [
            'Tmax구름의 검증된 보안성 및 사용성을 기반으로 사용자 환경에 최적화된 보안 단말'
          ],
        },
        {
          path: links.osp5114,
          text: 'SuperRDS',
          p: ['Tmax구름 SD와 결합한 안전하고 간편한 원격 접속 솔루션'],
        },
      ]
    },
    {
      h5: '홍보',
      path: links.osp6111,
      img_banner: 'img_banner-subpage-3',
      span: [
        '티맥스오에스의 언론 보도와 다양한 최근 소식을 제공합니다.',
      ],
      ul: [
        {
          path: links.osp6115,
          text: 'TmaxOS 소식',
          p: ['이벤트, 내/외부 행사, 서포터즈 활동 등 티맥스오에스의 다양한 활동과 소식을 제공합니다.']
        },
        {
          path: links.osp6112,
          text: '뉴스룸',
          p: ['티맥스오에스의 새로운 소식입니다.']
        },
        {
          path: links.osp6114,
          text: '홍보영상',
          p: ['티맥스오에스의 소식을 영상으로 만나보세요.']
        },
      ]
    },
    {
      h5: '고객지원',
      path: links.osp7111,
      img_banner: 'img_banner-subpage-4',
      span: [
        '티맥스오에스에 대한 궁금증을 편리하게 해결하세요.',
      ],
      ul: [
        {
          path: links.osp7116,
          text: '공지사항',
          p: ['회사와 제품에 대한 새로운 소식을 전달합니다.']
        },
        {
          path: links.osp7120,
          text: '자주하는 질문',
          p: ['티맥스오에스에 자주하는 질문과 답을 살펴보세요.']
        },
        {
          path: links.osp7121,
          text: '문의하기',
          p: ['티맥스오에스에 대해 궁금한 점을 문의해 주세요.']
        },
        {
          path: links.osp7126,
          text: '다운로드',
          p: ['Tmax구름을 무료로 체험하고 제품별 사용 방법을 확인해 보세요.']
        }
      ]
    },
  ];

  const [main, setMain] = useState(true);
  const [lnbTabs, setLnbTabs] = useState(false);
  const [pageState, setPageState] = useState({
    depth1_index: -1,
    depth2_index: -1,
    depth2_focus: -1,
    depth2_class: 'depth2-close',
  });

  const handleMainHeaderInteraction = () => {
    if (window.scrollY > 0) {
      setMain(false);
    }
    else if (window.scrollY === 0) {
      setMain(true);
    }
  }

  const handlePageCategory = () => {
    // const gnb = window.location.pathname.split('/')[1];
    const gnb = window.location.href.split('/').pop();
    // console.log(window.location.href)

    if (gnb === "") {
      setMain(true);
      return true;

    } else {
      setMain(false);

      return false;
    }
  };

  useEffect(() => {
    const mainPage = handlePageCategory();

    if (mainPage) {
      window.addEventListener('scroll', handleMainHeaderInteraction)
    }
    return () => {
      window.removeEventListener('scroll', handleMainHeaderInteraction)
    }
  }, [pageState.depth1_index]);

  let displayTimer;
  const onMouseEnter = (index: number = 0) => {
    setPageState({
      ...pageState,
      depth2_index: index,
      depth2_class: 'depth2-open',
    });

    clearTimeout(displayTimer);
  };

  const onMouseLeave = () => {
    displayTimer = setTimeout(() => {
      setPageState({
        ...pageState,
        depth2_class: 'depth2-close'
      });
    }, 300);
  };

  const handleGNBClick = (index) => {
    window.scrollTo(0, 0);
    setPageState({
      ...pageState,
      depth2_class: 'depth2-close',
      depth1_index: index,
    });

    // setLnbTabs(false);
  };

  const handleDepth2ButtonClick = (index_depth1, index_depth2) => {
    window.scrollTo(0, 0);

    setPageState({
      ...pageState,
      depth2_class: 'depth2-close',
      depth2_focus: index_depth2,
      depth1_index: index_depth1,
    });
  };

  const handleLNBClick = (index) => {
    setPageState({
      ...pageState,
      depth2_class: 'depth2-close',
      depth1_index: index,
    });

    // setPageState({
    // ...pageState,
    // depth2_class: 'depth2-close',
    // });
  }

  return <>
    <header className={main ? 'header header-main' : 'header header-sub'}>
      <nav>
        <div className='gnb__depth1 w-default'>
          <div className="--left">
            <div className="depth1__logo">
              <ButtonNaviLogoHome
                props={{
                  onClick: handleGNBClick
                }}
              />
            </div>
            <ul className="gnb-depth1">
              <li>
                <ButtonNaviGnb
                  props={{
                    path: links.osp4111,
                    index: 0,
                    pageIndex: pageState.depth1_index,
                    onMouseEnter: onMouseEnter,
                    onMouseLeave: onMouseLeave,
                    onClick: handleGNBClick,
                    text: "TmaxOS 소개"
                  }}
                />
              </li>
              <li>
                <ButtonNaviGnb
                  props={{
                    path: links.osp5111,
                    index: 1,
                    pageIndex: pageState.depth1_index,
                    onMouseEnter: onMouseEnter,
                    onMouseLeave: onMouseLeave,
                    onClick: handleGNBClick,
                    text: "제품"
                  }}
                />
              </li>
              <li>
                <ButtonNaviGnb
                  props={{
                    path: links.osp6111,
                    index: 2,
                    pageIndex: pageState.depth1_index,
                    onMouseEnter: onMouseEnter,
                    onMouseLeave: onMouseLeave,
                    onClick: handleGNBClick,
                    text: "홍보"
                  }}
                />
              </li>
              <li>
                <ButtonNaviGnb
                  props={{
                    path: links.osp7111,
                    index: 3,
                    pageIndex: pageState.depth1_index,
                    onMouseEnter: onMouseEnter,
                    onMouseLeave: onMouseLeave,
                    onClick: handleGNBClick,
                    text: "고객지원"
                  }}
                />
              </li>
            </ul>
          </div>

          <div className="--right">
            <ul className="depth1__community">
              <li>
                <Link to={links.osp0111}
                  onMouseEnter={() => onMouseEnter(4)}
                  onMouseLeave={onMouseLeave}
                  onClick={() => handleGNBClick(4)}
                  className={4 === pageState.depth1_index ? "focused" : ''}
                >Tmax구름커뮤니티</Link></li>
              <li><ButtonLinkHR /></li>
            </ul>

            <ul
              className="depth1__user"
              style={{ display: 'none' }}
            >
              {/* <li><Link to="/login" onClick={onClick}>로그인</Link></li>
              <li><Link to="/signin" onClick={onClick}>회원가입</Link></li> */}
            </ul>

            <div className="depth1__box">
              <button
                className="btn-toggle-depth1"
                onClick={() => setLnbTabs(!lnbTabs)}
              >
                {
                  lnbTabs ? <IconLnbClose /> : <IconHamberger />
                }
              </button>
            </div>
          </div>
        </div>

        <div className="gnb__depth2">
          {
            navSites.map(({ h5, span, ul }, index) => {

              const uls = ul.map((li, index_ul) => {
                return <li key={index_ul}>
                  <Link
                    to={li.path}
                    onClick={() => handleDepth2ButtonClick(index, index_ul)}
                  >
                    {li.text}
                  </Link>
                </li>
              });

              return <div
                key={index}
                onMouseEnter={() => onMouseEnter(index)}
                onMouseLeave={onMouseLeave}
                onClick={() => handleLNBClick(index)}
                className={'depth2 ' + (pageState.depth2_index === index ? pageState.depth2_class : 'depth2-close')}
              >
                <div className='depth2-container w-default'>
                  <div>
                    <h5>{h5}</h5>
                    <p>{span}</p>
                    <button style={{ display: 'none' }}>{"문의하기"}</button>
                  </div>
                  <ul>{uls}</ul>
                </div>
              </div>
            })
          }
        </div>
        {
          lnbTabs &&
          <div className="lnb">
            <ul className='lnb-depth1'>
              {
                navSites.map((li, index) => {
                  const focus = pageState.depth1_index === index;

                  const lnbDepth2 = li.ul.map((content, index_depth2) => {
                    return <li key={index_depth2}>
                      <a
                        href={process.env.PUBLIC_URL + content.path}
                        className="btn-lnb-depth2"
                        onClick={() => setLnbTabs(false)}
                      >
                        <span>{content.text}</span>
                      </a>
                    </li>
                  })

                  return <li key={index}
                    className={focus ? 'focused' : ''}>
                    <div
                      onClick={() => handleLNBClick(index)}
                    >
                      <ButtonDepthMore focus={focus} text={li.h5} />
                    </div>
                    {
                      focus &&
                      <ul className="lnb-depth2">
                        {lnbDepth2}
                      </ul>
                    }
                  </li>
                })
              }
            </ul>

            <ul className="lnb-depth1">
              <li>
                <Link to={links.osp0111}
                  className={"btn-lnb-depth1"}
                >Tmax구름커뮤니티
                </Link>
              </li>
              <li><ButtonLinkHR /></li>
            </ul>
          </div>
        }

      </nav>

    </header>
    <ButtonScrollTop />

    {/* { 
      pageState.depth1_index > -1 
      && 
      <Banner page={{ 
        code: pageState.depth1_index,
        subcode: pageState.depth2_focus,
      }}/>
    } */}
  </>

}

