import { useState } from 'react';

import Banner from '../../components/common/Banner';
import { TabBarStyle } from '../../components/common/TabStyle';


export default function OSP4117() {
  const [tabNumber, setTabNumber] = useState(0);

  const page = {
    code: 4,
    subcode: 2
  }

  const onLoad = () => {
    console.log("onLoad");
  }

  // 첫 번째 탭
  const Tab01 = () => {
    return <div className='map__wrapper w-default'>
      <address>
        <span className='addr-map'><i className='icon icon-circle-blue'></i>경기 성남시 분당구 황새울로 258번길 29</span>
        <p><span>Tel</span>031-776-8988 (평일 09:00~18:00)</p>
        <p><span>Mail</span>tmaxossupport@tmax.co.kr</p>
      </address>

      {/* 임시로 넣어둔 지도 */}
      <iframe
        title="temp_tmaxmap" className='temp-map'
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d792.612632796924!2d127.11624862924597!3d37.37917636442881!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357b5966ac99f18f%3A0x25aeedfa026f54d3!2zVG1heCBXQVBMICjti7Drp6XsiqTsmYDtlIwp!5e0!3m2!1sko!2skr!4v1648616383160!5m2!1sko!2skr"
        // allow='fullscreen'
        loading="lazy" referrerPolicy="no-referrer-when-downgrade"
        onLoad={() => onLoad()}
      ></iframe>

    </div>
  }


  // 두 번째 탭
  const Tab02 = () => {
    return <div className='map__wrapper w-default'>
      <address>
        <span className='addr-map'><i className='icon icon-circle-blue'></i>address of rnd center</span>
        <p><span>Tel</span>031-776-8988 (평일 09:00~18:00)</p>
        <p><span>Mail</span>tmaxossupport@tmax.co.kr</p>
      </address>

      {/* 임시로 넣어둔 지도 */}
      <iframe
        title="temp_tmaxmap" className='temp-map'
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d792.612632796924!2d127.11624862924597!3d37.37917636442881!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357b5966ac99f18f%3A0x25aeedfa026f54d3!2zVG1heCBXQVBMICjti7Drp6XsiqTsmYDtlIwp!5e0!3m2!1sko!2skr!4v1648616383160!5m2!1sko!2skr"
        // allow='fullscreen'
        loading="lazy" referrerPolicy="no-referrer-when-downgrade"
        onLoad={() => onLoad()}
      ></iframe>

    </div>
  }

  const toggleContents = (tabNumber) => {
    switch (tabNumber) {
      case 0:
        return <Tab01 />;
      case 1:
        return <Tab02 />;
      default:
        return <>
          잘못된 접근
          history 관리하기
        </>;
    }
  }

  return <>
    <Banner page={page} />
    <main className='main-osp4117'>

      <TabBarStyle props={{
        list: [
          '본사', 'R&D센터'
        ],
        setTabNumber,
        tabNumber
      }} />

      {toggleContents(tabNumber)}

    </main>
  </>
}