import Banner from "../../components/common/Banner"
import { CommuBoard } from "../../components/CommuBoard";

import { IconArrowRight } from "../../components/common/Icons";

export default function OSP0111() {
  const page = {
    code: 8,
    subcode: -1,
    community: true,
    inputCate: {
      banner: true,
      name: 'search',
      placeHolder: '검색어를 입력해 주세요.',
    }
  };

  return <>
    <Banner page={page} />

    <main className="main-osp0111 commu">
      <section className="community-main">
        <h1>
          <strong>공개 소프트웨어</strong>
          를 선도할 개발자들의 커뮤니티!
        </h1>
        <div className="menu-guide-list__wrapper w-default">
          <ul className="menu-guide-list">
            <li>
              <button>
              <i className="icon icon-commu-01"></i>
              <h4>개발 가이드</h4>
              <p>
                Tmax구름의 소스 코드 등을 활용하여
                자유롭게 개발해 보세요.
              </p>
              </button>
              
            </li>
            <li>
              <button>
              <i className="icon icon-commu-02"></i>
              <h4>오픈소스 포럼</h4>
              <p>
                다양한 오픈소스에 대한 정보와
                의견을 공유해
                보세요.
              </p>
              </button>
            </li>
            <li>
              <button>
              <i className="icon icon-commu-03"></i>
              <h4>사용 후기</h4>
              <p>
                Tmax구름 사용 후기를
                자유롭게 공유해 보세요.
              </p>
              </button>
            </li>
            <li>
              <button>
              <i className="icon icon-commu-04"></i>
              <h4>묻고 답하기</h4>
              <p>
                Tmax구름에 대해 궁금한 점을 묻고
                다른 사용자와 관리자의 답변을 받아보세요.
              </p>
              </button>
            </li>
          </ul>
        </div>
        <a href="/" className="btn btn-rounded-03 commu">
          <span>Tmax구름에 대해 더 알아보기</span>
          <IconArrowRight />
        </a>
      </section>

      <div className="temp-layout-grcommu">
        <div className="temp-message">
          <div className="icon-temp-img"></div>
          <span>커뮤니티 세부 페이지 추후 오픈 예정입니다.</span>
        </div>

      <section className="community-board of">
        <h3>오픈소스 포럼</h3>
        <div className="board-wrap w-default">
          <CommuBoard />
          <a className="btn btn-more commu" href="/">오픈소스 포럼 전체보기<i className="icon icon-more_arrow"></i></a>
        </div>
      </section>

      <section className="community-board ua">
        <h3>사용 후기</h3>
        <div className="board-wrap w-default">
          <CommuBoard />
          <a className="btn btn-more commu" href="/">사용 후기 전체 보기<i className="icon icon-more_arrow"></i></a>
        </div>
      </section>

      <section className="community-board qa">
        <h3>묻고 답하기</h3>
        <div className="board-wrap w-default">
          <CommuBoard />
          <a className="btn btn-more commu" href="/">묻고 답하기 전체 보기<i className="icon icon-more_arrow"></i></a>
        </div>
      </section>
      </div>

    </main>
  </>
}