import { ButtonMore } from './button/Buttons';
import { links } from '../data/link';


// 메인화면 OSP3111 | 홍보 OSP6111
const PromoteBoard = () => {
  const props = {
    path: links.osp6115,
    content: '새로운 소식 전체 보기'
  };

  return <>
    <section className="main-board">
      <div className="board__container w-default">
        <h3>
          <span>티맥스오에스의</span>
          <span><strong>새로운 소식</strong>을 만나보세요.</span>
        </h3>

        <div className="board-cards">
          <button>
            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="board-0" />
            <h5 className="board-title">티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h5>
          </button>

          <button>
            <img src="" alt="board-1" />
            <h5 className="board-title">티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h5>
          </button>

          <button>
            <img src="" alt="board-2" />
            <h5 className="board-title">티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h5>
          </button>

          <button>
            <img src="" alt="board-3" />
            <h5 className="board-title">티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h5>
          </button>
        </div>
        <ButtonMore props={props} />
      </div>
    </section>
  </>
}

// 메인화면 OSP3111 | 홍보 OSP6111
const PromoteNews = () => {
  const props = {
    path: links.osp6112,
    content: '뉴스룸 전체 보기'
  };

  return <>
    <section className="main-news">
      <div className="news__container w-default">
        <h3>뉴스룸</h3>

        <div className='news-grid'>
          <button className="top-news">
            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="top-news" />

            <div className='news-content'>
              <h4 className='news-title'>티맥스오에스, 구글 플랫폼 기반 ‘Tmax구름' 개발</h4>
              <p>티맥스오에스가 구름 플랫폼 기반 개방형 OS 'Tmax구름'을 개발했다. <br />
                운영체제 전문기업 티맥스오에스(대표이사 허희도)는 과학기술정보통신부가 지원하고 국가보안기술...</p>
              <p className='news-date'>2021.12.13</p>
            </div>
          </button>

          <button className='news-another'>
            <div className='news-content'>
              <h4 className='news-title'>티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h4>
              <p className='news-date'>2021.12.13</p>
            </div>

            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="news-02" />
          </button>

          <button className='news-another'>
            <div className='news-content'>
              <h4 className='news-title'>티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상  2021년 공개 SW 개발자 대회 자체 시상 2021년 공개 SW 개발자 대회 자체 시상</h4>
              <p className='news-date'>2021.12.13</p>
            </div>

            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="news-03" />
          </button>

          <button className='news-another'>
            <div className='news-content'>
              <h4 className='news-title'>티맥스오에스, 2021년 공개 SW 개발자 대회 자체 시상</h4>
              <p className='news-date'>2021.12.13</p>
            </div>

            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="news-04" />
          </button>
        </div>
        <ButtonMore props={props} />
      </div>
    </section>
  </>
}

// 메인화면 OSP3111 | 제품 OSP5112
const Customer = () => {
  return <>
    <section className='main-customer'>
      <div className='customer__container w-default'>

        <span className="img img-customer-banner"></span>

        <div className='banner-title'>
          <h5>무엇이든 물어보세요!</h5>

          <div className="temp-layout">
            <div className="inquiry-content">
              <span className="title">전화 문의</span>
              <address className="contact-number">031 - 776 - 8988</address>
              <p>평일 09:00 ~ 18:00</p>
            </div>

            <div className="inquiry-content">
              <span className='title'>이메일 문의</span>
              <address className="contact-email">tmaxossupport@tmax.co.kr</address>
              <p>
                토요일/일요일/공휴일에 이메일로 문의하신 건은 
                휴일 이후 평일에 답변 메일을 받으실 수 있습니다
              </p>
            </div>
          </div>
          {/* <p>1:1 문의를 통해 빠르게 답변드리겠습니다.</p> */}
        </div>

        {/* <a
          className='btn btn-rounded-02'
          href={links.osp7121}
        >
          <span>문의하기</span>
          <i className="icon icon-write"></i>
        </a> */}
      </div>
    </section>
  </>
}

export { PromoteBoard, PromoteNews, Customer };