import React from 'react'

import Banner from '../../components/common/Banner';
import { ContVideo } from './PromoteComponent';
import { Pagination } from '../../components/common/ForBoard';


export default function OSP6114() {
  const page = {
    code: 6,
    subcode: 2
  };
  
  return <>
    <Banner page={page} />

    <main className='main-osp6114'>
      <article className="main-promote">

        {/* dummy */}
        <iframe width="100%" height="1002" src="https://www.youtube.com/embed/ajKvZ7LZKfA" 
        title="YouTube video player" frameBorder="0" 
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>

        <button className="btn-main-video">
          <div className="btn__container">
            <i className="icon icon-play"></i>
            <h2>티맥스오에스, 구름 플랫폼 기반‘Tmax구름’개발</h2>
          </div>
        </button>

      </article>

      <article className='promotion-video'>
        <section className='wrap-item video w-default'>
          <ul className='media-list'>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            <li>
              <ContVideo />
            </li>
            
          </ul>

          <Pagination />

        </section>
      </article>
    </main>
  </>
}
