const ButtonNaviGnb = ({ props }) => {
  const {
    path,
    index,
    pageIndex,
    onMouseEnter,
    onMouseLeave,
    onClick,
    text
  } = props;

  // console.log(pageIndex)

  return <>
    <Link
      to={path}
      onMouseEnter={() => onMouseEnter(index)}
      onMouseLeave={onMouseLeave}
      onClick={() => onClick(index)}
      className={pageIndex === index ? "focused" : ""}
    >
      {text}
    </Link>
  </>
}


export {
  ButtonNaviLogoHome,
  ButtonNaviGnb,
};