import { Link } from "react-router-dom"

import { links } from "../../data/link";
// import { InputSearchBanner } from "./InputSearch";


export default function Banner({ page }) {
  const gnbDepth2 = [
    {
      h5: 'TmaxOS 소개',
      path: links.osp4111,
      img_banner: 'img_banner-subpage-1', // img넣을 class이름 
      span: [
        '개방형 OS 시장을 선도하고 신사업을 발굴해 다방면의 서비스를 제공합니다.',
      ],
      ul: [
        {
          path: links.osp4111,
          text: '회사소개',
          p: [
            '개방형 OS 시장을 선도하고 신사업을 발굴해 다방면의 서비스를 제공합니다.',
          ],
        },
        {
          path: links.osp4116,
          text: '연혁',
          p: [
            '대한민국을 넘어 글로벌 SW기업으로 도약하기 위해 도전하고 상상합니다.',
          ],
        },
        {
          path: links.osp4117,
          text: '오시는 길',
          p: [
            '대한민국을 넘어 글로벌 SW기업으로 도약하기 위해 도전하고 상상합니다.'
          ],
        },
      ]
    },
    {
      h5: '제품',
      path: links.osp5111,
      img_banner: 'img_banner-subpage-2',
      span: [
        'Tmax구름, SuperPCMaster 등 티맥스오에스의 제품을 소개합니다.',
      ],
      ul: [
        {
          path: links.osp5112,
          text: 'Tmax구름',
          p: [
            '국가보안기술연구소의 구름 보안 프레임워크에',
            'TmaxOS 자체 개발 기술력으로 완성한 개방형 운영 체제'
          ],
        },
        {
          path: links.osp5113,
          text: 'Tmax구름 SD',
          p: [
            'Tmax구름의 검증된 보안성 및 사용성을 기반으로 사용자 환경에 최적화된 보안 단말'
          ],
        },
        {
          path: links.osp5114,
          text: 'SuperRDS',
          p: ['Tmax구름 SD와 결합한 안전하고 간편한 원격 접속 솔루션'],
        },
      ]
    },
    {
      h5: '홍보',
      path: links.osp6111,
      img_banner: 'img_banner-subpage-3',
      span: [
        '티맥스오에스의 언론 보도와 다양한 최근 소식을 제공합니다.',
      ],
      ul: [
        {
          path: links.osp6115,
          text: 'TmaxOS 소식',
          p: ['이벤트, 내/외부 행사, 서포터즈 활동 등 티맥스오에스의 다양한 활동과 소식을 제공합니다.']
        },
        {
          path: links.osp6112,
          text: '뉴스룸',
          p: ['티맥스오에스의 새로운 소식입니다.']
        },
        {
          path: links.osp6114,
          text: '홍보영상',
          p: ['티맥스오에스의 소식을 영상으로 만나보세요.']
        },
      ]
    },
    {
      h5: '고객지원',
      path: links.osp7111,
      img_banner: 'img_banner-subpage-4',
      span: [
        '티맥스오에스에 대한 궁금증을 편리하게 해결하세요.',
      ],
      ul: [
        {
          path: links.osp7116,
          text: '공지사항',
          p: ['회사와 제품에 대한 새로운 소식을 전달합니다.']
        },
        {
          path: links.osp7120,
          text: '자주하는 질문',
          p: ['티맥스오에스에 자주하는 질문과 답을 살펴보세요.']
        },
        {
          path: links.osp7121,
          text: '문의하기',
          p: ['티맥스오에스에 대해 궁금한 점을 문의해 주세요.']
        },
        {
          path: links.osp7126,
          text: '다운로드',
          p: ['Tmax구름을 무료로 체험하고 제품별 사용 방법을 확인해 보세요.']
        }
      ]
    },
    {
      h5: 'Tmax구름 커뮤니티',
      path: links.osp0111,
      img_banner: 'img_banner-subpage-5',
      span: [
        'Tmax구름과 다양한 오픈소스에 대한 정보와 의견을 공유하세요.',
      ],
      ul: [
        {
          path: links.osp0112,
          text: '개발 가이드',
          p: [
            'Tmax구름의 소스 코드 등을 활용하여 자유롭게 개발해 보세요.'
          ],
        },
        {
          path: '/app/OSP0114',
          text: '오픈소스 포럼',
          p: [
            '다양한 오픈소스에 대한 정보와 의견을 공유해 보세요.'
          ],
        },
        {
          path: '/app/OSP011401',
          text: '사용 후기',
          p: [
            'Tmax구름 사용 후기를 자유롭게 공유해 보세요.'
          ],
        },
        {
          path: '/app/OSP011402',
          text: '묻고 답하기',
          p: [
            'Tmax구름에 대해 궁금한 점을 묻고 다른 사용자와 관리자의 답변을 받아보세요.'
          ],
        }
      ]
    },
    {
      h5: '마이 페이지',
      path: '/app/OSP9112',
      img_banner: 'img_banner-subpage-6',
      span: [
        '회원정보와 커뮤니티 활동 관리',
      ],
      ul:[]
    }
  ];
  
  const inf = { 
    community: false,
    inputCate : {
      banner: false,
      placeholder: 'default',
    },
    download: false,
    ...page
  };

  const bannerClass = inf.community ? 'subpage-banner commu' : 'subpage-banner';

  const returnContent = (pageCode = -1) => {
    if (pageCode < 0) { // 개요 페이지 입니다.
      const { h5, span } = gnbDepth2[page.code - 4];
      return {
        head: h5, text: span, bannercode: -1
      }
    }
    else { // 서브 페이지 입니다. 
      const { text, p } = ul[pageCode];
      return {
        head: text, text: p, bannercode: pageCode
      }
    }
  };

  const makeulLi = (uls) => {
    return uls.map((li, index) => {
      return <li key={index}>
        <Link to={li.path}
          className={bannercode === index ? 'focused' : ''}
        >{li.text}</Link>
      </li>
    });
  };

  const { ul, img_banner } = gnbDepth2[page.code - 4]; // ex) OSP5111 - code: 5 -> 5-4  --> 
  const { head, text, bannercode } = returnContent(page.subcode);

  const uls = makeulLi(ul);

  return <>
    <section className={bannerClass}>
      <span className={"illust-subpage " + img_banner}></span>
      {/* 서브 메뉴 */}
      <div className='subpage-depth2'>
        <ul className='depth2__container w-default'>{uls}</ul>
      </div>
      {/* 서브 내용 */}
      <div className='subpage-cont w-default'>
        <h2>{head}</h2>
        <p className={inf.code === 5 && inf.subcode > -1 ? 'important': ' '}>
          {
            text.map((item, index) => {
              return <span key={index}>
                {item}
              </span>
            })
          }
        </p>
        {inf.download && 
          <a 
            href='#download' 
            className='btn-rounded-02 btn-download-tmaxgr' 
            download={false}
          >
            다운로드 
          </a>
        }
        {/* {inf.inputCate.banner && <InputSearchBanner page={page} />} */}
      </div>
    </section>
  </>
}