import { useState } from 'react';



const FormInputType1 = ({props}) => {
  const [ inputVal, setInputVal ] = useState('');
  const [ typed, setTyped ] = useState(false);

  const inf = {
    label: 'label',
    type: 'text',
    required: false,
    class: 'value',
    ...props
  };

  const onChange = (event) => {
    const { target: { value }} = event;

    setInputVal(value);
  };

  const textfieldClass = (state) => {
    const {typed, inputVal} = state;

    if(inputVal.length > 0) {
      return 'typed';
    }
    else{
      return typed ? 'typed' : ''; 
    }
  }

  return <div className={`textfield --t1 ${textfieldClass({typed, inputVal})}`}>
  
  <label>{inf.label}</label>

  <input
    type={inf.type}
    className={inf.class}
    required={inf.required}
    onClick={(event)=>{
      event.stopPropagation();
      setTyped(!typed);
    }}
    value={inputVal}
    onChange={(event) => onChange(event)}
  />
  </div>
}


const FormInputType2 = ({props}) => {
  const [ inputVal, setInputVal ] = useState('');

  const onChange = (event) => {
    const { target: { value }} = event;

    setInputVal(value);
  };

  const inf = {
    tagInput: true,
    class: 'value',
    type: 'text',
    placeHolder: 'default',
    required: false,
    ...props
  };

  const textfieldClass = (state) => {
    const { inputVal } = state;

    if(inputVal.length > 0){
      return 'typed';
    }
    else{
      return ''
    }
  }

  return <div className={`textfield --t2 ${textfieldClass({inputVal})}`}>
  {
    inf.tagInput // input tag인가
    ?
    <input 
      type={inf.type}
      className={inf.class}
      required={inf.required}
      placeholder={inf.placeHolder}

      value={inputVal}
      onChange={(event) => onChange(event)}
    />
    :
    <textarea
      type={inf.type}
      className={inf.class}
      required={inf.required}
      placeholder={inf.placeHolder}

      value={inputVal}
      onChange={(event) => onChange(event)}
    >
    </textarea>
  }
  </div>
}


const DropBox = ({props}) => {
  const [dropdown, setDropdown] = useState(false);

  const inf = {
    label: 'label',
    type: 'text',
    required: false,
    class: 'value',
    // list: ['Tmax구름', 1, 2, 3],
    ...props
  };


  return <>
  <button 
    className='dropbox'
    onClick={(e) => {
      e.preventDefault(); // required 메세지 계속 나옴 
      setDropdown(!dropdown);
    }}
  >

    <label htmlFor="category">{inf.label}</label>

    <div className={dropdown? 'value focused' : 'value'}>
      <i className='icon icon-dropbox'></i>
    </div>

    <input value={'dummy'} readOnly />

  </button>


  {
    dropdown &&
    <div className="dropdown">
      <ol className=''>

        <li><button
          onClick={() => {
            // setSelect('Tmax구름');
            setDropdown(false);
          }}
        >Tmax구름</button></li>
        <li><button>2</button></li>
        <li><button>3</button></li>
        <li><button>4</button></li>
      </ol>
    </div>
  }
  </>
}

export { FormInputType1, FormInputType2, DropBox };