import Banner from '../../components/common/Banner';
// import { tosProducts } from '../../data/memo';

import { IconDownLoad, IconOpenNew } from '../../components/common/Icons';

export default function OSP7126() {
  const page = {
    code: 7,
    subcode: 3
  };

return <>
    <Banner page={page} />
    <main className='main-osp7126 custom'>
      <section className='products-list background-01'>
        <h2>
          티맥스오에스제품이 처음이신가요? <br />
          <strong>아래의 가이드를 활용해보세요.</strong>
        </h2>

        {/* product 리스트 */}
        <ul className='products width1218'>
          {/* 첫번째만 레이아웃이 다름 */}
          <li className='product tmax-gr'>
            {/* 제품 정보 */}
            <div className="product-infor">
              <h4>Tmax구름</h4>
              <p>국가보안기술연구소의 구름 보안 프레임워크에 TmaxOS 자체 개발 기술력으로 완성한 개방형 운영체제</p>
              {/* 제품 상세 스펙  */}
              <div className="tmaxgr-spec">
                <span className='spec-cate'>권장 사양</span>
                <ul className='spec-list'>
                  <li>
                    <span className="spec-tit">CPU</span>
                    <span>2.5 GHz 2 core (Intel 3세대 이상) </span>
                  </li>
                  <li><span className="spec-tit">디스크</span><span>20GB</span></li>
                  <li><span className="spec-tit">메모리</span><span>8GB</span></li>
                  <li><span className="spec-tit">해상도</span><span>1024x768</span></li>
                </ul>
              </div>
            </div>
            {/* 버튼들  */}
            <div className="btns-download">

              <a href="#downloads" className="btn btn-rounded-01 --install">
                <span>설치파일  (.iso)</span>
                <IconDownLoad />
              </a>

              <a href="#downloads" className="btn btn-rounded-03">
                <span>설치가이드</span>
                <IconDownLoad />
              </a>

              <a href="#downloads" className="btn btn-rounded-03">
                <span>사용가이드</span>
                <IconOpenNew />
              </a>
            </div>
          </li>

          <li className='product'>
            <div className="product-infor">
              <h4>Tmax구름 SD</h4>
              <p>Tmax구름의 검증된 보안성 및 사용성을 기반으로 사용자 환경에 최적화된 보안 단말</p>
            </div>
            <div className="btns-download">
              <a href="#downloads" className="btn btn-rounded-03">
                <span>사용가이드</span>
                <IconOpenNew />
              </a>
            </div>
          </li>

          <li className='product'>
            <div className="product-infor">
            <h4>SuperRDS</h4>
            <p>Tmax구름 SD와 결합한 안전하고 간편한 원격 접속 솔루션</p>
            </div>
            <div className="btns-download">
              <a href="#downloads" className="btn btn-rounded-03">
                <span>사용가이드</span>
                <IconOpenNew />
              </a>
            </div>
          </li>
        </ul>
      </section>
    </main>
  </>
}