import { useState } from 'react';

import Banner from '../../components/common/Banner';

import { TabBarStyle } from '../../components/common/TabStyle';
import { QuestionBoard } from '../../components/CustomBoard';
import { Pagination } from '../../components/common/ForBoard';

import { InputSearchBoard } from '../../components/common/InputSearch';


export default function OSP7120() {
  const [tabNumber, setTabNumber] = useState(0);

  const page = {
    code: 7,
    subcode: 1,
    inputCate: {
      banner: false,
      name: "search-table",
      placeHolder: '제목 또는 내용 검색'
    }
  };

  return <>
    <Banner page={page} />

    <TabBarStyle 
      props = {{
        list : [
          '전체', 'Tmax구름', 'SuperPCMaster', 'SuperVDS', '기타'
        ],
        tabNumber,
        setTabNumber
      }}
    />

    <div className='div-osp7120 width1352 custom'>
      <InputSearchBoard page={page} />
      <div className='board-wrap question'>
        <QuestionBoard />
        <Pagination nums={[1, 2, 3, 4, 5]} />
      </div>
    </div>
  </>
}