const ButtonScrollTop = () => {
  const handleScrollTop = () => {
    window.scrollTo(0, 0);
  };

  return <>
  <button className="btn-scroll-top" onClick={handleScrollTop}>
    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path d="M13.7976 5.8036C13.7976 5.31629 14.1926 4.92124 14.6799 4.92124H15.2579C15.7452 4.92124 16.1403 5.31629 16.1403 5.8036V26.912C16.1403 27.3993 15.7452 27.7943 15.2579 27.7943H14.6799C14.1926 27.7943 13.7976 27.3993 13.7976 26.912V5.8036Z" fill="#666666"/>
    <path d="M14.9998 3.45166C14.7742 3.45109 14.5485 3.53646 14.3759 3.70785L7.706 10.3299C7.3593 10.6741 7.35829 11.2345 7.70375 11.58L8.11246 11.9887C8.45617 12.3324 9.01311 12.3334 9.35805 11.9909L14.9998 6.38966L20.6415 11.9909C20.9864 12.3334 21.5434 12.3324 21.8871 11.9887L22.2958 11.58C22.6412 11.2345 22.6402 10.6741 22.2935 10.3299L15.6237 3.70785C15.451 3.53646 15.2253 3.45109 14.9998 3.45166Z" fill="#666666"/>
    </svg>
  </button>
  </>
};

const ButtonDepthMore = (props) => {
  const {focus, text} = props;

  return <>
  <button className='btn-lnb-depth1'>
    <span>{text}</span>
    <i className="icon">
      <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
      {
        focus
        ?
        <path d="M8.6 7.4L2.6 7.4C2.26862 7.4 2 7.66863 2 8C2 8.33137 2.26862 8.6 2.6 8.6L8.6 8.6L13.4 8.6C13.7314 8.6 14 8.33137 14 8C14 7.66863 13.7314 7.4 13.4 7.4L8.6 7.4Z" fill="#2C99FF"/>
        :
        <path d="M8.6 2.59999C8.6 2.26862 8.33137 1.99999 8 1.99999C7.66863 1.99999 7.4 2.26862 7.4 2.59999L7.4 7.4L2.6 7.4C2.26862 7.4 2 7.66863 2 8C2 8.33137 2.26862 8.6 2.6 8.6L7.4 8.6V13.4C7.4 13.7314 7.66863 14 8 14C8.33137 14 8.6 13.7314 8.6 13.4L8.6 8.6L13.4 8.6C13.7314 8.6 14 8.33137 14 8C14 7.66863 13.7314 7.4 13.4 7.4L8.6 7.4L8.6 2.59999Z" fill="#666666" />
      }
      </svg>
    </i>
  </button>
  </>
}

export {
  ButtonScrollTop,
  ButtonDepthMore
};